<template>
 <el-container> 
    <el-header>
      <el-row>
        <el-col :span="24">
          <el-card>
            <div>
              <label>字典名称：</label>
              <el-input
                placeholder="请输入内容"
                
                style="width: 200px;"
                v-model="input1"
              ></el-input
              >&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <label>字典列名：</label>
              <el-input
                placeholder="请输入内容"
                
                style="width: 200px;"
                v-model="input2"
              ></el-input>
              <div style="float: right;">
                <el-button type="primary" @click="systemSearch">搜索</el-button>
                <el-button type="info" @click="clear">重置</el-button>
                <el-button type="primary" @click="dialogFormVisible = true"
                >添加</el-button>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-header>
    <br />
    <el-main>
      <!--
      <el-card>
        <el-row>
          <el-col :span="12">
              <label style="font-size:20px">码表列表:</label>
          </el-col>
          <el-col :span="12">
            <div style="float: right;">
              <el-button type="primary" @click="dialogFormVisible = true"
                >添加</el-button>
            </div>
          </el-col>
        </el-row>
      </el-card>
      -->
      <br />
      <el-card>
        <el-row>
          <el-col :span="24">
            <el-table
              :data="
                tableData.slice(
                  (currentPage - 1) * pageSize,
                  currentPage * pageSize
                )
              "
              style="width: 100%;"
            >
              <el-table-column
                prop="dictName"
                label="字典名称"
                
              ></el-table-column>
              <el-table-column
                prop="dictColumn"
                label="字典列名"
                
              ></el-table-column>
              <el-table-column
                prop="code"
                label="代码"
                
              ></el-table-column>
              <el-table-column
                prop="name"
                label="取值"
                
              ></el-table-column>
              <el-table-column
                prop="remark"
                label="备注"
                
              ></el-table-column>
              <el-table-column
                prop="opTime"
                label="配置时间"
                
              ></el-table-column>
            </el-table>
          </el-col>
        </el-row>
        <!--分页-->
    
      <div class="block">
        <span class="demonstration"></span>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="tableData.length"
        ></el-pagination>
      </div>
    
      </el-card>
    </el-main>
<!--添加窗口-->
    <el-dialog
      title="码表添加"
      :visible.sync="dialogFormVisible"
      @close="dialogClosed"
    >
      <el-form :model="dictForm" :rules="rules" ref="dictForm">
        <el-form-item
          label="字典名称"
          :label-width="formLabelWidth"
          prop="dictName"
        >
          <el-input v-model="dictForm.dictName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item
          label="字典列名"
          :label-width="formLabelWidth"
          prop="dictColumn"
        >
          <el-input v-model="dictForm.dictColumn" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="代码" :label-width="formLabelWidth" prop="code">
          <el-input v-model="dictForm.code" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="取值" :label-width="formLabelWidth" prop="name">
          <el-input v-model="dictForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="说明" :label-width="formLabelWidth" prop="remark">
          <el-input v-model="dictForm.remark" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button
          type="primary"
          @click="add('dictForm'), (dialogFormVisible = false),(dialogClosed)"
          >提交</el-button
        >
      </div>
    </el-dialog>

 </el-container>
</template>

<script>
export default {
  data() {
    return {
      tableData: [
        {
          pageCount: 99,
          pageIndex: "",
          currentPage: 1,
          pageSize: [5, 10, 15, 20],
          rowCount: 99,
          dictName: "",
          dictColumn: "",
          code: "",
          name: "",
          remark: "",
          opTime: "",
        },
      ],
      //表单验证规则
      rules: {
        dictName: [{ required: true, message: "不能为空", trigger: "blur" }],
        dictColumn: [{ required: true, message: "不能为空", trigger: "blur" }],
        code: [{ required: true, message: "不能为空", trigger: "blur" }],
        name: [{ required: true, message: "不能为空", trigger: "blur" }],
        remark: [{ required: false, message: "随便写点", trigger: "blur" }],
      },

      //表单初始化
      dictForm: {
        dictName: "",
        dictColumn: "",
        code: "",
        name: "",
        remark: "",
        opTime: "",
      },
      //数据初始化
      dialogTableVisible: false,
      dialogFormVisible: false,
      input1: "",
      input2: "",
      formLabelWidth: "120px",
      currentPage: 1,
      pageSize: 10,
    };
  },
  //页面初始化
  mounted: function () {
    var param = {};
    this.request.postJson(this.mockapi.dictQueryByPage, param).then((res) => {
      (this.tableData = []), (this.tableData = res.data);
    });
  },

  methods: {
    //关闭刷新
    dialogClosed() {
      this.systemSearch();
    },
    //分页
    handleSizeChange(val) {
      this.currentPage = 1;
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },
    //搜索模糊查询
    systemSearch: function () {
      var param = {
        dictName: this.input1,
        dictColumn: this.input2,
        pageSize: this.pageCount,
        pageIndex: this.currentPage,
      };
      this.request.postJson(this.mockapi.dictQueryByPage, param).then((res) => {
        (this.tableData = []), (this.tableData = res.data);
      });
    },
    //添加
    add: function (dictForm) {
      //表单验证
      this.$refs[dictForm].validate((valid) => {
        console.log(valid);
        if (valid) {
          //接口调用
          let formData = this.dictForm;
          this.request
            .postJson(this.mockapi.dictAdd, formData)
            .then((res) => {});
          this.$message({
            message: "添加成功！",
            type: "success",
          });
          this.clear();
        } else {
          this.$message({
            message: "添加失败！",
            type: "failed",
          });
          this.clear();
        }
      });
    },
    //输入框重置
    clear: function () {
      this.input1 = "";
      this.input2 = "";
      this.dictForm = "";
    },
  },
};
</script>
<style src="../home/index.css" scoped></style>



